<template>
  <v-data-table
    :headers="headers"
    :items="allPatientsNormalized"
    item-key="name"
    class="elevation-1"
    :search="search"
    :custom-filter="searchFilter"
  >
    <template v-slot:top>
      <v-text-field
        v-model="search"
        v-mask="searchMask"
        :label="'Поиск по ' + searchModeString"
        class="mx-4"
      />
    </template>
    <template v-slot:item="{ item }">
      <tr>
        <td>{{ item.fullName }}</td>
        <td>{{ item.snills.replace(/(\d)(?=(\d{4})+$)/g, '$1 ') }}</td>
      </tr>
    </template>
  </v-data-table>
</template>

<script>
  import allPatients from './all-patients'
  export default {
    data () {
      return {
        search: '',
        allPatients,
        headers: [
          {
            text: 'ФИО',
            value: 'fullName',
            sortable: false,
          }, {
            text: 'СНИЛС',
            value: 'snills',
            sortable: false,
          },
        ],
      }
    },
    computed: {
      searchMode () { // 1-инпут пуст, 2-поиск по СНИЛС 3-поиск по ФИО
        if (this.search.length === 0) {
          return 1
        } else if (!isNaN(parseInt(this.search[0], 10))) {
          return 2
        } else {
          return 3
        }
      },
      searchMask () {
        if (this.searchMode === 2) {
          return '### ### ### ##'
        } else {
          return null
        }
      },
      searchFilter () {
        let cb
        if (this.searchMode !== 2) {
          cb = (value, search, item) => {
            return value != null && search != null && typeof value !== 'boolean' && value.toString().toLocaleLowerCase().indexOf(search.toLocaleLowerCase()) !== -1
          }
        } else {
          cb = (value, search, item) => {
            return value != null && search != null && typeof value !== 'boolean' && value.toString().toLocaleLowerCase().indexOf(search.replace(/\s+/g, '')) !== -1
          }
        }
        return cb
      },
      searchModeString () {
        if (this.searchMode === 1) {
          return 'ФИО или СНИЛС'
        } else if (this.searchMode === 2) {
          return 'СНИЛС'
        } else {
          return 'ФИО'
        }
      },
      allPatientsNormalized () {
        return allPatients.map(el => ({
          fullName: `${el.lastName} ${el.name} ${el.patronymic}`,
          snills: el.snills.replace(/\s+/g, ''),
        }))
      },
    },
    watch: {
      search () {
        // console.log(this.search)
      },
    },
    created () {
      // console.log(allPatients)
    },
  }
</script>
